import React from 'react'
import './Image.css';
import doctor1 from './images/doctor1.png';
import doctor2 from './images/doctor2.png';
import doctor3 from './images/doctor3.png';
import doctor4 from './images/doctor4.png';

function Image() {
  return (
    <div id="Our Team" className="Our Team">
      <h1>Our Team</h1>
      <h1>Dr. David Lahijaniha</h1>
      <img src={doctor1} alt=" this is pic1" />
      <h3>Dr. David Lahijaniha’s love for animals started at a young age and inspired him to be the loving and dedicated veterinarian he is today. He enjoys all areas of veterinary medicine, but looks forward to helping the intricate and complex challenges of internal medicine and surgery. His special interests are in emergency and critical care. </h3>
      <h1>Paola, Lead Nurse</h1>
      <img src={doctor2} alt=" this is pic2" />
      
      <h3>Paola started her career in the veterinary field in 2009, received her Certificate as a Veterinary Assistant in 2010, and is now in the process of continuing education working on her Veterinary Technicians License. Paola has been a technician in the field for many years with many rave reviews.</h3>
      <h1>Jason Matney, Manager</h1>
      <img src={doctor3} alt=" this is pic3" />
      
      <h3>Jason obtained his first position in a veterinary hospital in 1996. Long before that time, he found his love for animals as a pet owner and hobbyist, keeping and studying the needs of his many pets, from hamsters to horses. </h3>
      <h1>Selena Sanchez, Assistant</h1>
      <img src={doctor4} alt=" this is pic4" />
      
      <h3>Selena is one of the Veterinary Assistant here at Animal Medical Center. She is originally from Sylmar California, but raised in Palmdale. She continued her dream at High Desert Medical College and graduated this year in November 2020. The love Selena has for animals motivated her to start a career in health care of animals. Her continuing interest in the veterinary community can help enrich the human animal bond. She plans to move forward in becoming a Registered Veterinary Technician</h3>


      </div>
  )


}

export default Image;