import React from 'react'
import './Nav.css';
import Link from './Link.js';
import './Contact';






function Nav() {
    return (
        <div className="Nav">
            <Link name="Home" />
            <Link name="About" />
            <Link name="Our Team" />
            <Link name="Our Services" />
            <Link name="Contact" />
        
        </div>
    );
}

export default Nav;